import { CardTypeGroup } from "../../core/src/main";
export const imageUrlType = (card) => `https://ygoprodeck.com/pics/icons/${encodeURIComponent(card.type.name)}.jpg`;
export const imageUrlSubType = (card) => card.type.group === CardTypeGroup.MONSTER
    ? `https://ygoprodeck.com/pics/${encodeURIComponent(card.subType)}.png`
    : `https://ygoprodeck.com/pics/icons/${encodeURIComponent(card.subType)}.png`;
export const imageUrlAttribute = (card) => `https://ygoprodeck.com/pics/${encodeURIComponent(card.attribute)}.jpg`;
export const imageUrlAtk = () => "https://ygoprodeck.com/wp-content/uploads/2017/01/atk.png";
export const imageUrlLevel = () => "https://ygoprodeck.com/wp-content/uploads/2017/01/level.png";
export const imageUrlLinkMarker = () => "https://ygoprodeck.com/wp-content/uploads/2019/04/link-arrow-right.png";
export const imageUrlBanState = (card, format) => `https://ygoprodeck.com/pics/icons/${encodeURIComponent(card.banlist[format].name)}.png`;
export const imageUrlCardPlaceholder = () => "https://ygoprodeck.com/pics/4035199.jpg";
