/* eslint-disable @typescript-eslint/naming-convention */
import { CardTypeGroup } from "../../../core/card/type/CardTypeGroup";
import { DeckPart } from "../../../core/deck/DeckPart";
import { findByKey, requireNonNilElseThrow } from "lightdash";
const mapGroup = (type) => requireNonNilElseThrow(findByKey(CardTypeGroup, type.group), () => new TypeError(`Unexpected type group '${type.group}'.`));
const mapDeckPart = (type) => new Set(type.area.map((area) => requireNonNilElseThrow(findByKey(DeckPart, area), () => new TypeError(`Unexpected deck part '${area}'.`))));
const mapTypes = (typeNames, types) => typeNames.map((typeName) => requireNonNilElseThrow(types.find((type) => type.name === typeName), () => new TypeError(`Could not find type '${typeName}'.`)));
